<template>
  <v-row align="center" justify="center">
    <v-col cols="12" md="8" lg="9">
      <h1 v-text="interactiveMenu['title']"></h1>
    </v-col>
    <v-col cols="12" md="4" lg="3">
      <v-btn fab @click.stop="dialog = true" class="v-btn__border">
        <v-icon :color="button_color">fas fa-chart-bar</v-icon>
      </v-btn>
      <v-dialog v-model="dialog" max-width="800">
        <v-card>
          <v-card-title
            class="headline justify-center"
            v-text="interactiveMenu['helpNavigation']['interpretation']['title']"
          >
          </v-card-title>
          <v-card-text v-text="interactiveMenu['helpNavigation']['interpretation']['description']">
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialog = false">
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-btn fab @click.stop="dialog_1 = true" class="v-btn__border">
        <v-icon :color="button_color">fas fa-file-pdf</v-icon>
      </v-btn>
      <v-dialog v-model="dialog_1" max-width="800">
        <v-card>
          <v-card-title class="headline"> </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialog_1 = false">
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-btn :color="button_color" dark fab @click.stop="dialog_2 = true">
        <v-icon>fas fa-question</v-icon>
      </v-btn>
      <v-dialog v-model="dialog_2" max-width="800">
        <v-card>
          <v-card-title
            class="headline justify-center"
            v-text="interactiveMenu['helpNavigation']['help']['title']"
          />
          <v-card-text
            v-text="interactiveMenu['helpNavigation']['help']['description']"
          />
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialog_2 = false">
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-col>
  </v-row>
</template>
<style>
.v-btn__border {
  border: 3px solid #043349;
  padding: auto;
  margin: 0 20px 0 0;
}
</style>
<script>
export default {
  name: 'ReportBar',
  props: {
    interactiveMenu: {
      type: Object,
      default: () => ({
        title: 'titulo',
        helpNavigation: {
          help: {
            title: 'esta es la ayuda',
            image: 'foto',
            description: 'dd'
          },
          download: {
            graphics: []
          },
          interpretation: {
            title: 'esta es la interpretacion',
            image: 'imagen',
            description: 'descripcion de la interpretacion'
          }
        }
      })
    }
  }

  ,

  data() {
    return {
      dialog: false,
      dialog_1: false,
      dialog_2: false,
      button_color: '#043349'
    }
  }
}
</script>
