<template>
  <v-container fluid>
    <report-bar :interactiveMenu="SpecificInformation" />
    <course-communication
      v-if="CurrentRoute == 'course-communication'"
      :graph="SpecificInformation"
    />
    <evaluation-panic
      v-if="CurrentRoute == 'evaluation-panic'"
      :graph="SpecificInformation"
    />
    <interest-in-the-type-of-resource
      v-if="CurrentRoute == 'interest-in-the-type-of-resource'"
      :graph="SpecificInformation"
    />
    <least-viewed-course-resources
      v-if="CurrentRoute == 'least-viewed-course-resources'"
      :graph="SpecificInformation"
    />
    <repetitive-resources
      v-if="CurrentRoute == 'repetitive-resources'"
      :graph="SpecificInformation"
    />
    <student-interaction-with-the-course
      v-if="CurrentRoute == 'student-interaction-with-the-course'"
      :graph="SpecificInformation"
    />
    <module-effectiveness
      v-if="CurrentRoute == 'module-effectiveness'"
      :graph="SpecificInformation"
    />
  </v-container>
</template>


<script>
export default {
  name: 'Report',
  data: () => ({
    reportt: []
  }),
  computed: {
    CurrentRoute() {
      return this.$route.params.current
    },
    SpecificInformation() {
      let currentRoute = this.$route.params.current.replaceAll('-', '_')
      switch (currentRoute) {
        case 'module_effectiveness':
          return module_effectiveness
        case 'course_communication':
          return course_communication
        case 'evaluation_panic':
          return evaluation_panic
        case 'interest_in_the_type_of_resource':
          return interest_in_the_type_of_resource
        case 'least_viewed_course_resources':
          return least_viewed_course_resources
        case 'repetitive_resources':
          return repetitive_resources
        case 'student_interaction_with_the_course':
          return student_interaction_with_the_course
        default:
          console.log('problemas')
          break
      }
    }
  }
}
</script>

<style></style>
