import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import vuetify from "./plugins/vuetify";
import BarNavigation from './components/BarNavigation.vue'
import CourseCommunication from './reports/CourseCommunication.vue'
import EvaluationPanic from './reports/EvaluationPanic.vue'
import InterestInTheTypeOfResource from './reports/InterestInTheTypeOfResource.vue'
import LeastViewedCourseResources from './reports/LeastViewedCourseResources.vue'
import ModuleEffectiveness from './reports/ModuleEffectiveness.vue'
import RepetitiveResources from './reports/RepetitiveResources.vue'
import StudentInteractionWithTheCourse from './reports/StudentInteractionWithTheCourse.vue'
import ReportBar from './components/ReportBar.vue'
Vue.config.productionTip = false;

Vue.component('BarNavigation', BarNavigation)
Vue.component('ReportBar', ReportBar)
Vue.component('CourseCommunication', CourseCommunication)
Vue.component('EvaluationPanic', EvaluationPanic)
Vue.component('InterestInTheTypeOfResource', InterestInTheTypeOfResource)
Vue.component('LeastViewedCourseResources', LeastViewedCourseResources)
Vue.component('RepetitiveResources', RepetitiveResources)
Vue.component('ModuleEffectiveness', ModuleEffectiveness)
Vue.component(
  'StudentInteractionWithTheCourse',
  StudentInteractionWithTheCourse
)
new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount("#app");
