<template>
  <v-container fill-height>
    <v-row align="center" justify="center">
      <v-col cols="6" sm="5" md="4" v-for="img in imgs" :key="img">
        <v-img :src="getImgUrl(img)" max-height="300" />
      </v-col>
      <v-col cols="12">
        <h2 v-text="title"></h2>
        <p v-text="description"></p>
      </v-col>
      <v-col cols="12">
        <router-link
          v-for="buttom in buttoms"
          :key="buttom.title"
          :to="{ name: buttom.route, params: { current: buttom.InitCurrent } }"
        >
          <v-btn class="ma-2" tile outlined v-text="buttom.title"> </v-btn>
        </router-link>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: 'Welcome',
  data: () => ({
    title: 'Bienvenidos',
    description: `Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's
       standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. 
       It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised 
       in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus 
       PageMaker including versions of Lorem Ipsum.`,
    buttoms: [
      { title: 'Inicio', route: 'report', InitCurrent: 'module-effectiveness' },
      { title: 'Tutorial', route: 'Tutorial' }
    ],
    imgs: ['no-image2.png', 'no-image.png']
  }),
  methods: {
    getImgUrl(img) {
      var images = require.context('../assets/img/', false, /\.png$/)
      return images('./' + img)
    }
  }
}
</script>