import Vue from "vue";
import VueRouter from "vue-router";
import Welcome from "../views/Welcome.vue";
import Tutorial from "../views/Tutorial.vue";
import Report from "../views/Report.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Welcome",
    component: Welcome
  },
  {
    path: "/tutorial",
    name: "Tutorial",
    component: Tutorial
  },
  {
    path: "/report/:current?",
    name: "report",
    props: true,
    component: Report
  },
];

const router = new VueRouter({
  routes
});

export default router;
