<template>
  <div>
    <v-app-bar app clipped-left :src="getImgUrl(background)" height="90">
      <v-icon
        class="not-hover spacer-right"
        large
        color="white"
        @click="drawer = !drawer"
        >fas fa-bars</v-icon
      >
      <v-spacer></v-spacer>
      <div class="full-height vertical-center d-none d-lg-flex ">
        <img :src="getImgUrl(logo_ideclab)" height="60%" class="spacer-right" />
        <img :src="getImgUrl(logo_udec)" height="60%" />
      </div>
      <div class="background-select-group">
        <v-select
        class=" mt-1 pt-4 pl-15 pr-4 "
          @change="saveGroup($event)"
          :items="group"
          v-model="selectedGroup"
          label="Seleccione un grupo"
          solo
        ></v-select>
      </div>
    </v-app-bar>

    <v-navigation-drawer app v-model="drawer" fixed clipped flat>
      <v-list nav class="not-padding-left">
        <v-list-item class="d-flex d-lg-none">
          <img
            :src="getImgUrl(background)"
            class="navigation-drawer-background"
          />
          <v-row class="forward">
            <v-col cols="6">
              <img :src="getImgUrl(logo_ideclab)" class="full-width" />
            </v-col>
            <v-col cols="6">
              <img :src="getImgUrl(logo_udec)" class="full-width" />
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item-group v-model="selectedItem">
          <router-link
            v-for="item in optReports"
            :key="item.title"
            :to="{ name: 'report', params: { current: item.id } }"
          >
            <v-card class=" card" flat elevation="1">
              <v-list-item class="text-start">
                <v-list-item-title v-text="item.title"></v-list-item-title>
              </v-list-item>
            </v-card>
          </router-link>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>
<style scoped>
.container-select-group {
  background: rgb(255, 255, 255);
  width: 15%;
  right: 0px;
  height: 90px;
  display: block;
  position: absolute;
  z-index: 1;
}

.container-select-group::before {
  content: '';
  position: absolute;
  left: -38px;
  width: 100px;
  height: 90px;
  background: rgb(255, 255, 255);
  transform: skew(-40deg);
}
.vertical-center {
  display: flex;
  align-items: center;
}
.full-height {
  height: 100%;
}
.v-list-item--active {
  border-left: 10px solid #043349;
  color: white !important;
}
.v-list-item__title {
  color: black;
}
.card {
  border-radius: 0px !important;
  margin: 10px 0 10px 0;
}
.not-padding-left {
  padding-left: 0px !important;
}
.v-icon.v-icon:after {
  width: 0;
}
.spacer-right {
  padding-right: 15px;
}
.navigation-drawer-background {
  height: 90px;
  width: 100%;
  position: fixed;
  left: 0;
}
.forward {
  z-index: 1;
}
.full-width {
  width: 100%;
}
.background-select-group{
  position: inherit;
  right: -16px;
  width: 23em;
  height: 90px;
background: linear-gradient(117deg, transparent 0,transparent 14.6%,#FFF 10%,#FFF 50.4%,#FFF 100%)
}
</style>
<script src="<%= BASE_URL %>data/BarNavigation.js"></script>
<script>
export default {
  name: 'BarNavigation',
  data: () => ({
    group: ['Grupo uno', 'Grupo dos', 'Grupo tres', 'Grupo cuatro'],
    selectedItem: 0,
    selectedGroup: null,
    drawer: null,
    background: 'background_bar.png',
    logo_udec: 'logo_udec.png',
    logo_ideclab: 'logo_ideclab.png',
    optReports: [],
  }),
  methods: {
    getImgUrl(img) {
      var images = require.context('../assets/img/', false, /\.png$/)
      return images('./' + img)
    },
    getOptReports: function() {
      Items.map((item, index) => {
        this.selectedItem =
          item['id'] == this.$route.params.current ? index : this.selectedItem
        this.optReports.push(item)
      })
    },
    saveGroup(event) {
      localStorage.selectedGroup = event;
    },

    CurrentSelectedGroup(){
      if(localStorage.selectedGroup) {
      this.selectedGroup = localStorage.selectedGroup;
    }
    
    }

  },
  mounted: function() {
    this.getOptReports(),
    this.CurrentSelectedGroup()
  }

}
</script>
